import './App.css';
import { Photo } from './component/photo';


import React, { useState } from 'react';
import { QrReader } from 'react-qr-reader';


function App() {
  const [data, setData] = useState('No result');

  
  return (
    <div>
      <Photo />
      <div style={{
            position: 'fixed',
            bottom: '10%',
            left: '50%',
            backgroundColor: '#4bff00',
            color: 'white',
            fontWeight: 500,
            padding: '30px',
            marginLeft: '-24%',
            fontSize: '30px',
      }}>
        version: 3
      </div>
      <>
        <QrReader
          onResult={(result, error) => {
            if (!!result) {
              setData(result?.text);
            }

            if (!!error) {
              console.info(error);
            }
          }}
          style={{ width: '100%' }}
        />
        <p>{data}</p>
      </>
    </div>
  );
}

export default App;
